.element__btn-like {
  background: transparent url(../../../images/btn-like.svg) center center
    no-repeat;
  width: 22px;
  height: 19px;
  border: none;
  outline: none;
  padding: 0;
  transition: all 0.2s linear;
}

.element__btn-like:hover {
  opacity: 0.5;
  cursor: pointer;
}
