.profile__avatar-edit {
  border-radius: 50%;
  width: 120px;
  height: 120px;
  margin: 0 30px 0 0;
  padding: 0;
  outline: none;
  border: none;
  background: transparent;
  transition: background-color 0.3s linear;
  position: absolute;
}

.profile__avatar-edit:hover {
  background: rgba(0, 0, 0, 0.8) url(../../../images/btn-edit-avatar.svg) center /
    26px no-repeat;
  outline: 1px solid black;
  cursor: pointer;
}

@media screen and (max-width: 1023px) {
  .profile__avatar-edit {
    margin: 0 0 26px;
  }
}
